export var getSubRouteGroup = function (route) {
    /// [/sub/path/.../branching/][leaf]
    var m = /(?<branches>(?:\/.*\/?)?\/)(?<leaf>[^\/]*)$/
        .exec(route);
    return m ? m.groups : { branches: "", leaf: "" };
};
export var getNextSegmentOrLeaf = function (mapping, i) {
    return i < (mapping.length - 1) ? mapping[i + 1].route : "?";
};
